.footer{
  padding: 20px 0;
  background-color: #2b2b2b;

  &__inner{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__navigation{

    &-link{
      margin-left: 10px;
      color: #ffffff;
      font-size: 14px;
      text-transform: capitalize;
      &:hover{
        color: #ff1493;
      }
    }
  }

}

.copyright{
  width: 100%;
  margin-top: 10px;
  text-align: center;
  text-transform: capitalize;
  font-size: 12px;
  color: #ffffff;
}