.whore-cabinet{
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;

  &__content{
    flex: 1;
    align-self: stretch;
    padding: 15px 0;
    background-color: #ffffff;
  }

  &__title{
    margin: 0;
    text-transform: uppercase;
    text-align: center;

    &-after{
      text-align: center;
      font-size: 14px;
    }

    &-medium{
      width: 100%;
      margin: 0;
      text-align: center;
      text-transform: uppercase;
      &:not(:first-child){
        margin-top: 15px;
      }
    }
  }

  &__tab{
    display: block;
  }

  &__link{
    display: block;
    padding: 10px;
    background-color: #9400d3;
    font-size: 14px;
    text-transform: uppercase;
    color: white;
    border-left: 2px solid deeppink;
    border-bottom: 1px solid white;
    &:hover, &_active{
      background-color: deeppink;
    }
    .whore-cabinet__tab:last-child &{
      border-bottom: none;
    }
  }


  &__column{

    &-third{
      width: 33.333333%;
      padding: 0 15px;
    }

    &-fourth{
      width: 25%;
      padding: 0 15px;
      margin-bottom: 15px;
    }
  }


  &__form{
    display: flex;
    flex-wrap: wrap;
    padding: 15px;


    &-center{
      width: 100%;
    }

    &-notation{
      width: 100%;
      margin-top: 15px;
      padding: 0 15px;
      font-size: 14px;
    }
  }

  &__work{

    &-day{
      min-width: 100px;
      font-size: 14px;
      font-weight: bold;
      text-transform: capitalize;
    }

    &-doubled{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-bottom: 15px;
    }

    &-column{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
  }

  &__gallery{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 5px;

    &-main{
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      margin-top: 5px;
    }

    &-wrapper{
      display: flex;
      flex-wrap: wrap;

      & form{
        width: 100%;
      }
    }

    &-item{
      position: relative;
      width: 25%;
      padding: 5px;

      &-remove{
        position: absolute;
        top: 0;
        right: 0;
        width: 20px;
        height: 20px;
        line-height: 20px;
        @include border-radius(100%);
        background-color: #c100c1;
        color: #ffffff;
        text-align: center;
        font-size: 12px;
        text-transform: uppercase;
        cursor: pointer;
      }

      & img{
        display: block;
        margin: 0 auto;
        width: auto;
        max-height: 165px;
      }
    }
  }

  &__statictic{

    &-line-chart{
      position: relative;
      margin: auto;
      height: 50vh;
      width: 90%;
    }
  }

}

.label-whore-cabinet-work{
  margin: 0 10px;
  font-size: 14px;
}

.select-whore-cabinet-work{
  display: block;
  height: 28px;
  font-size: 14px;
  border: none;
  border-bottom: 2px solid #8b008b;
}

.label-gallery-whore-cabinet{
  padding: 10px 30px;
  font-size: 14px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: deeppink;
  cursor: pointer;
}

.inp-gallery{
  display: none;
  & + button{
    margin-right: 0;
  }
}

.go-whore-cabinet{
  display: inline-block;
  margin: 0 15px;
  padding: 10px;
  font-size: 14px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #333333;
  cursor: pointer;

  & i{
    margin-right: 10px;
  }
}

.btn-admin--whore-index{
  display: inline-block;
}