.single{
  display: flex;
  flex-wrap: wrap;
  font-size:$single-fz;

  &__gallery{
    width: 40%;
    padding: 0 15px;

    &-prev, &-next{
      position: absolute;
      top: 50%;
      color: $girl-bg;
      transform: translate(0, -50%);
      cursor: pointer;
      z-index: 3;
    }

    &-prev{
      left: -20px;
    }

    &-next{
      right: -20px;
    }
  }

  &__gallery-helper{
    position: relative;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    &-item{
      margin: 0 5px;
      outline: none;
      cursor: pointer;
    }
  }

  &__info{
    width: 60%;
    padding: 0 15px;
    & > *{
      padding: 15px;
      border-bottom: 1px solid $line;
    }
    & > *:last-child{
      padding-bottom: 0;
      border-bottom: none;
    }
    & > *:first-child{
      padding-top: 0;
    }
  }

  &__top{
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &-friends{
      margin-left: auto;
      font-size: 14px;
      color: #000000;
      text-transform: capitalize;
      cursor: pointer;
      &:hover{
        color: #ff1493;
      }
    }
  }

  &__name{
    margin-left: 10px;
    font-size: 1.4em;
  }

  &__friends{
    display: none;

    &-item{
      float: left;
      width: 25%;
      padding: 5px;
    }

    &-item a{
      display: block;
      border: 1px solid #8B008B;
    }

    &-info{
      display: block;
      padding: 3px;
      background-color: #000000;
      color: #ffffff;
      font-size: 14px;
      text-align: center;
      border-top: 1px solid #ffffff;
    }
  }

  &__contacts{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    &-main{
      display: flex;
      flex-wrap: wrap;
    }

    &-additional{
      display: flex;
      flex-wrap: wrap;
      margin-top: 15px;
    }

    &-item{
      display: flex;
      flex-wrap: wrap;
      margin-right: 15px;
    }

    &-ico{
      margin-right: 5px;
      color: #8b008b;
    }

    &-text{
      font-size: 16px;
    }
  }

  &__general{
    display: flex;
    flex-wrap: wrap;

    &-item{
      width: 25%;
      margin: 10px 0;
      text-transform: capitalize;
    }

    &-label{
      font-weight: bold;
    }
    &-value{
      margin-top: 5px;
    }
  }

  &__price{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    &-row{
      display: flex;
      flex-wrap: wrap;
    }

    &-item{
      flex: 1;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    }

    &-label{
      margin-right: 10px;
      text-transform: uppercase;
    }

    &-value{
      padding: 10px;
      background-color: $girl-bg;
      color: $single-price-color;
      font-weight: bold;
    }
  }

  &__available{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &-title{
      width: 100%;
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 10px;
    }
  }

  &__working{
    display: flex;
    flex-wrap: wrap;

    &-schedule{
      flex:1;
    }

    &-conditions{
      flex:1;
    }

    &-title{
      font-size: 18px;
      font-weight: bold;
      & > i{
        margin-right: 10px;
      }
    }

    &-payments{

    }
    &-payment{
      margin-top: 10px;
      font-size: 14px;
    }

    &-offers{
      margin-top: 20px;
    }

    &-offer{
      margin-top: 10px;
      font-size: 14px;
    }

    &-item{
      display: flex;
      flex-wrap: wrap;
      margin-top: 10px;
      &_current{
        color: #8b008b;
      }
    }

    &-day{
      min-width: 120px;
      text-transform: capitalize;
    }

    &-hours{
      font-weight: bold;
    }
  }

  &__services{
    display: flex;
    flex-wrap: wrap;

    &-title{
      width: 100%;
      margin-bottom: 10px;
      font-size: 18px;
      font-weight: bold;
      text-transform: capitalize;
    }

    &-column{
      flex: 1;
    }

    &-item{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-bottom: 5px;
    }

    &-sign{
      color: $girl-bg;
    }

    &-value{
      margin-left: 5px;
    }
  }
}