html, body{
  margin: 0;
  padding: 0;
  width: 100%;
}
body {
  font: 16px $font-stack;
  color: $primary-color;
}
*{
  box-sizing: border-box;
}

.wrapper{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  min-height: 100vh;
}
.container{
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  @media all and (max-width: 768px){
    padding: 0 15px;
  }
}
.content{
  flex: 1;
  padding: 15px 0;
  &--enter{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background: url("../images/enter-4.jpg") no-repeat center center;
    background-size: cover;
    & .container{
      position: relative;
      z-index: 1;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(0,0,0,0.75);
    }
  }
}
.clear:after, .clear:before{
  content: '';
  display: table;
  clear: both;
}

.ma-x{
  margin: 0 auto;
}

.mb-15{
  margin-bottom: 15px !important;
}

.w-100{
  width: 100%;
}
.tac{
  text-align: center;
}

ul{
  margin: 0;
  padding: 0;
  list-style:none;
}
img{
  display: block;
  width: 100%;
  max-width: 100%;
}
a, input, button, textarea, select {
  text-decoration: none !important;
  outline: none !important;
  transition: all 250ms;
}


.logo{
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &-img{
    width: 50px;
    height: 50px;
    background: url("../images/logo-3.png") no-repeat center center, #ffffff;
    background-size: auto 90%;
    @include border-radius(100%);
  }

  &-text{
    margin-left: 10px;
    font-size: 16px;
    text-transform: uppercase;
    color: #ffffff;
  }
}

@keyframes vip-status {
  from{
    background: $girl-bg;
  }
  to{
    background-color: $girl-bg-vip;
  }
}