.faq{

  &__item{
    margin-bottom: 15px;
    border: 2px solid $line;

    &_opened .faq__question-sign i{
      transform: rotate(90deg);
      transition: all 250ms;
    }
  }

  &__question{
    background-color: $line;
    color: #ffffff;
    cursor: pointer;
    
    &-sign{
      display: inline-block;
      vertical-align: middle;
      width: 40px;
      height: 40px;
      background-color: #fff;
      color: $line;
      line-height: 40px;
      text-align: center;
    }

    &-text{
      display: inline-block;
      vertical-align: middle;
      padding: 10px;
    }
  }

  &__answer{
    display: none;

    &-text{
      padding: 10px;
      border-top: 2px solid $line;
    }

    .faq__item_opened &{
      display: block;
    }
  }
}