.header{
  padding: 10px 0;
  background-color: $header-bg;
  color: #ffffff;
  font-size: 16px;

  &__inner{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__enter{
    padding: 10px;
    background-color: darkviolet;
    color: #ffffff;
    text-transform: capitalize;
    &:hover{
      background-color: deeppink;
    }
  }

  &__logged-in{
    font-size: 14px;
    text-transform: capitalize;
    color: #ffffff;

    & a{
      display: inline-block;
      margin-left: 5px;
      color: #ffffff;
    }
  }
}