.navigation{
  background-color: darkmagenta;
}

.navigation__list-item{
  float: left;
  &--enter{
    float: right;
  }
}

.navigation__link{
  display: block;
  padding: 10px;
  border-bottom: 2px solid transparent;
  font-size: $navigation-link-fz;
  text-transform: capitalize;
  color: #ffffff;
  &_active, &:hover{
    background-color: deeppink;
    border-bottom: 2px solid aqua;
  }
}