.search{
  margin-bottom: 15px;
  padding: 15px 15px 5px 15px;
  @include border-radius(15px);
  border: 1px solid darkmagenta;
}

.search__form{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.search__form-column{
  @media all and (max-width: 768px){
    width: 50%;
  }
}

.search__form-field{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content:flex-end;
  margin-bottom: 10px;
}

.search__form-field-doubled{
  display: flex;
  flex-wrap: wrap;
  justify-content:flex-end;
}

.search__advanced{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  margin: 5px 0;
  padding-top: 15px;
  border-top: 1px solid darkmagenta;
  &--closed{
    display: none;
  }

  &-title {
    width: 100%;
    margin-bottom: 15px;
    text-align: center;
  }

  &-item{
    margin-bottom: 5px;
  }
}



.filter{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px;
  @include border-radius(15px);
  background-color: darkmagenta;
}

.filter-view,
.filter-sort{
  display: flex;
  flex-wrap: wrap;
}

.filter-view__item{
  margin: 0 5px;
  cursor: pointer;
  color: #ffffff;
  &_selected{
    color: aqua;
  }
}